body {
  background-color: #13496D;
  font-size: 1.2rem;
  font-family: "freight-sans-pro";
  font-weight: 400;
  line-height: 1.6rem; }

strong, b {
  font-weight: 600; }

header, .layout-main-wrapper {
  background-color: #fff; }

header .banner {
  padding: 0.7rem 0 0 0; }
  header .banner .navbar-brand {
    padding: 0;
    margin-right: 2.9rem; }

header .dfci-banner-logo .field--type-image img {
  margin-bottom: 0; }

header .search-block-form .container-inline > .container-inline {
  display: block; }

header .search-form .form-type-search {
  width: 70%; }

header .search-form .form-control {
  border-radius: 0; }

header .search-form input[type="search"] {
  border-color: #13496D;
  line-height: 1.0; }

header .search-form input[type="submit"] {
  background-color: #13496D;
  padding-top: 0;
  padding-bottom: 0;
  border: none;
  height: 30px; }

header #search-block-form {
  padding: 0; }

#navbar-main.navbar {
  border-top: #00BDFF 3px solid;
  padding-top: 0.2rem;
  padding-bottom: 0; }

.menu--main .nav-link {
  color: #13496D;
  font-family: "realist";
  font-weight: 400;
  text-transform: uppercase;
  font-size: 0.95rem;
  border-bottom: #fff 6px solid;
  margin: 0 0.7rem;
  padding-top: 0.1rem;
  padding-bottom: 0.2rem;
  text-decoration: none; }

.menu--main .nav-link:hover,
.menu--main .nav-link:focus,
.menu--main .nav-link.is-active {
  color: #00BDFF;
  border-color: #00BDFF; }

.navbar-expand-md .navbar-nav .nav-link {
  padding-right: 0.8rem;
  padding-left: 0.8rem; }

.mission-statement {
  flex: 0 0 100%;
  margin-bottom: 1.6rem; }
  .mission-statement .statement-container {
    background-color: rgba(23, 54, 75, 0.7);
    width: 65%;
    text-align: center;
    padding: 1.0rem 0; }
    .mission-statement .statement-container h1 {
      letter-spacing: 0.04rem;
      color: #F4CE90;
      font-family: "rift";
      font-weight: 600;
      font-size: 1.7rem;
      margin: 0;
      line-height: 2.4rem;
      border: none; }

.homepage-tiles {
  background-image: url("../images/hero_bkgrd.jpg");
  background-size: cover;
  padding-bottom: 4.0rem; }
  .homepage-tiles .homepage-tile {
    margin: 15px 0;
    border: #13496D 1px solid;
    font-family: "frank-new";
    background-color: #fff; }
    .homepage-tiles .homepage-tile h2 {
      margin: 0;
      font-size: 1.25rem;
      font-weight: 400; }
      .homepage-tiles .homepage-tile h2 a {
        display: block;
        color: #fff;
        background-color: #13496D;
        padding: .8rem 1.2rem;
        border-bottom: #00BDFF 9px solid; }
      .homepage-tiles .homepage-tile h2 a, .homepage-tiles .homepage-tile h2 a:hover {
        text-decoration: none; }
      .homepage-tiles .homepage-tile h2 a:hover {
        color: #F4CE90; }
    .homepage-tiles .homepage-tile .content {
      padding: 1.0rem 1.0rem 1.2rem 2.0rem; }
      .homepage-tiles .homepage-tile .content .item-list ul {
        list-style: square url("../images/homepage_list_bullet.png"); }
        .homepage-tiles .homepage-tile .content .item-list ul li {
          line-height: 1.4rem;
          padding-bottom: 0.6rem; }
      .homepage-tiles .homepage-tile .content a {
        font-size: 1.1rem;
        color: #000;
        font-weight: 500;
        text-decoration: underline; }
      .homepage-tiles .homepage-tile .content a:hover {
        color: #00BDFF; }

.homepage-news-grid {
  margin-top: 2.5rem; }
  .homepage-news-grid h3 {
    line-height: 1.9rem;
    font-family: "frank-new";
    font-weight: 400; }
  .homepage-news-grid h3 a {
    color: #077ED6;
    text-decoration: none;
    font-size: 1.4rem; }

.title-decor {
  margin-bottom: 1.6rem;
  margin-left: -2.0rem;
  border-bottom: #13496D 1px solid; }

/** SERVICES **/
.service-container {
  margin-bottom: 2.4rem; }
  .service-container p.service-description {
    font-style: italic;
    margin-bottom: 0.8rem; }
  .service-container p {
    margin: 0; }
  .service-container .service-label {
    font-weight: 600;
    font-size: 1.33rem; }

.view-services h2 {
  margin-bottom: 0; }

/** GROUP PAGE **/
.group-mission,
.group-services,
.group-key-contact,
.group-project-highlight {
  margin-bottom: 2.1rem; }
  .group-mission ul li,
  .group-services ul li,
  .group-key-contact ul li,
  .group-project-highlight ul li {
    line-height: 2.2rem; }

.group-mission {
  font-size: 1.5rem;
  line-height: 1.8rem; }
  .group-mission .mission-label {
    font-weight: 600;
    color: #13496D; }

.group-key-contact h3 {
  margin-top: 1.8rem;
  font-style: italic; }

.group-project-highlight .paragraph--type--project-image {
  padding-left: 1.1rem; }

.group-project-highlight .field--name-field-project-title {
  font-weight: 600;
  font-size: 1.4rem;
  margin: 1.1rem 0; }

.group-project-highlight .field--name-field-image {
  border-top: #13496D 16px solid; }

.group-project-highlight .field--type-image img {
  margin-bottom: 0.8em; }

.group-project-highlight .field--name-field-image-caption {
  line-height: 1.4rem;
  font-size: 1.1rem;
  font-style: italic;
  padding: 0 1.0rem 1.1rem 1.0rem;
  border-bottom: #00BDFF 3px solid; }

/** FAST FIND **/
.index-col-3 {
  column-count: 3;
  -moz-column-count: 3; }

/** COMMON **/
h1 {
  border-bottom: #00BDFF 3px solid;
  font-family: "rift";
  font-weight: 400;
  font-size: 2.8rem;
  display: inline-block;
  color: #13496D;
  padding: 0 2.0rem;
  line-height: 3.4rem;
  margin-bottom: -3px; }

h2 {
  color: #077ED6;
  font-family: "frank-new";
  font-weight: 400;
  font-size: 1.5rem; }

h3 {
  color: #077ED6;
  font-family: "frank-new";
  font-weight: 400;
  font-size: 1.25rem; }

a {
  color: #00BDFF;
  font-weight: 500;
  text-decoration: underline;
  font-family: "frank-new";
  font-size: 1.16rem;
  transition: color, 0.5s; }

a:hover, a:focus, a:active {
  color: #13496D; }

.page-border-and-margin {
  margin-top: 2.0rem;
  padding-top: 3.3rem;
  padding-bottom: 4.0rem; }

.block-page-title-block {
  margin-bottom: 2.6rem; }

/** FOOTER **/
.site-footer {
  padding: 2.5rem 0 6.0rem; }
  .site-footer .block {
    border: none;
    margin: 0;
    padding: 0; }

.site-footer__top .region {
  float: none;
  width: 100%; }

.site-footer__top p {
  margin: 0; }

.menu--footer .navbar-nav {
  display: inline-block; }
  .menu--footer .navbar-nav li {
    float: left;
    padding: 0 0.6rem;
    border-right: #fff 1px solid; }
  .menu--footer .navbar-nav li:last-child {
    border: none;
    padding-right: 0; }
  .menu--footer .navbar-nav .nav-link {
    color: #fff;
    text-decoration: underline;
    padding: 0.1rem 0 0.2rem;
    line-height: 1.1rem;
    font-size: 0.95rem; }

.footer-copyright-privacy {
  font-size: 0.9rem;
  line-height: 1.1rem; }
  .footer-copyright-privacy a {
    color: #fff;
    text-decoration: underline;
    font-size: 0.9rem;
    font-family: "freight-sans-pro"; }

@media (min-width: 1600px) {
  .container {
    max-width: 1540px; }
  .col-xxl-7 {
    max-width: 58.3333333%;
    flex: 0 0 58.3333333%; }
  .col-xxl-6 {
    max-width: 50%;
    flex: 0 0 50%; }
  .col-xxl-5 {
    max-width: 41.66666%;
    flex: 0 0 41.66666%; }
  .col-xxl-4 {
    max-width: 33.3333333%;
    flex: 0 0 33.3333333%; }
  .col-xxl-3 {
    max-width: 25%;
    flex: 0 0 25%; }
  .offset-xxl-3 {
    margin-left: 25%; }
  .offset-xxl-4 {
    margin-left: 33.3333333%; }
  .offset-xxl-5 {
    margin-left: 41.66666666%; } }

@media (min-width: 1200px) and (max-width: 1599px) {
  header .dfci-banner-logo {
    padding-left: 3.0rem; } }

/* only for retina displays */
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .homepage-tiles {
    background-image: url("../images/hero_bkgrd@2x.jpg"); } }
